<template>
	<div class="container">
		<Sidebar 
		:folderStructure="folderStructure"
		:setFolderStructure="setFolderStructure"
		:setFileSelected="setFileSelected"
		:selectedFilePath="selectedFilePath"
		/>
		<div class="right-pane">
			<div class="header">
				{{userName}}
			</div>
			<div class="text-pane">
				<textarea class="text-content" @focus="setEditedFile" v-model="fileText" @blur="setFileContent"></textarea>
				<button class="btn" @click="saveAll">Save</button>
			</div>
		</div>
	</div>
</template>
<script>
import Sidebar from './../Sidebar/Sidebar.vue';
import { saveFolderStructure, getFolderStructure, saveFiles, getFile } from './../../services/index.js';
export default {
	props:{
		toggleLoader:Function
	},
	components:{
		Sidebar
	},
	data:()=>({
		userName:'Cedric',
		folderStructure:null,
		oldFolderStructure:null,
		editedFiles:null,
		selectedFilePath:'',
		editedFiles:[],
		fileText:''
	}),
	created(){
		getFolderStructure()
		.then(resp=>{
				if(resp.data && resp.data.folderStructure){
					this.folderStructure = resp.data.folderStructure;
					this.oldFolderStructure = JSON.parse(JSON.stringify(resp.data.folderStructure));
					return resp.data.folderStructure.files[0] || null
				}
		})
		.then((path)=>{
			if(path){
				this.selectedFilePath = path+'.txt';
				return getFile(path+'.txt')
			}
		})
		.then((resp)=>{
			if(resp.data){
				this.fileText = resp.data;
			}
		})
		.catch(()=>{
			console.log('Something went wrong');
		});
	},
	methods:{
		setFolderStructure(obj){
			this.folderStructure = obj;
		},
		saveAll(){
			let promise1, promise2;
			if(JSON.stringify(this.oldFolderStructure) !== JSON.stringify(this.folderStructure)){
				promise1 = saveFolderStructure({
					folderStructure:this.folderStructure
				})
			}
			else{
				promise1 = Promise.resolve();
				console.log('structure not changed')
			}

			let body;
			if(this.editedFiles.length>0){
				body = {
					"files":[]
				}
				this.editedFiles.forEach(path=>{
					const content = window.sessionStorage.getItem(path);
					const file = {
						path,
						content
					}
					body.files.push(file)
				})
				promise2 = saveFiles(body)
			}
			else{
				promise2 = Promise.resolve();
			}

			this.toggleLoader();
			Promise.all([promise1, promise2])
			.then(()=>{
				console.log('Saved successfully')
			})
			.catch(()=>{
				console.log('Something went wring')
			})
			.finally(()=>{
				this.toggleLoader();
			})
		},
		setFileSelected(path){
			this.selectedFilePath = path;

			//find file content in storage
			const sessionStorage = window.sessionStorage;
			const text = sessionStorage.getItem(path);
			if(text){
				this.fileText = text;
				return;
			}

			//if not in storage check from api
			this.toggleLoader();
			getFile(path)
			.then((resp)=>{
				//if found set in storage
				if(resp.data){
					this.fileText = resp.data;
					this.setFileContent();
				}
			})
			.catch(()=>{
				this.fileText = ''
			})
			.finally(()=>{
				this.toggleLoader();
			})
		},
		setEditedFile(){
			if(this.editedFiles.indexOf(this.selectedFilePath) < 0){
				this.editedFiles.push(this.selectedFilePath);
			}
		},
		setFileContent(){
			const sessionStorage = window.sessionStorage;
			sessionStorage.setItem(this.selectedFilePath,this.fileText)
		}
	}
}
</script>
<style scoped src="./Dashboard.css">
</style>