<template>
	<div class="left-pane">
		<ul class="root">
			<li @click="setFolderCreateOption(true);toggleFolders($event,0);">
				<img src="./../../assets/caret.png" class="caret">
				<img src="./../../assets/rootFolder.png">
			</li>
			<li  class="files" v-for="(files,i) in (folderStructure && folderStructure.files)" :key="'rootFiles'+i" @click="openFile($event,`${files}.txt`)" :class="setClass(`${files}.txt`)">
				<img src="./../../assets/file.png" class="file-icons">
				<span>{{files}}</span>
			</li>
			<li class="folders" v-for="(folders,i) in (folderStructure && folderStructure.folders)" @click="setFolderCreateOption(false);toggleFolders($event,'0'+i)" :key="i">
					<img src="./../../assets/caret.png" class="caret">
					<img src="./../../assets/folder.png" class="file-icons">
					<span>{{folders.name}}</span>
					<ul class="root">
						<li class="files" v-for="(files,i) in folders.files" :key="'files'+i" @click="openFile($event,`${folders.name}/${files}.txt`)">
							<img src="./../../assets/file.png" class="file-icons">
							<span>{{files}}</span>
						</li>
					</ul>
			</li>
		</ul>
		<div class="create-folder-panel" v-show="showCreatePanel" :style="{left:panelPos.left,top:panelPos.top}">
			<div class="close-icon" @click="toggleCreatePanel">x</div>
			<div @click="openFileFolderNamePanel">Create File</div>
			<div v-show="createFolderEnabled" @click="openFileFolderNamePanel(1)">Create Folder</div>
		</div>
		<div class="blurBg" v-if="showFileFolderNamePanel">
			<div class="file-folder-name" >
				<div class="file-name-label">
				<span>{{fileFolderNameLabel}}</span>
				<span class="cross" @click="toggleFileFolderNamePanel">x</span>
				</div>
				<input type="text" v-model="fileFolderName" class="file-folder-name-input">
				<p>
					<span class="btn" @click="createFilesOrFolder">SUBMIT</span>
				</p>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	props:{
		folderStructure:Object,
		setFolderStructure:Function,
		setFileSelected:Function,
		selectedFilePath:String
	},
	data:()=>({
		createFolderEnabled:false,
		clicks:0,
		showCreatePanel:false,
		showFileFolderNamePanel:false,
		selectedFolderPath:null,
		fileFolderName:'',
		fileFolderNameLabel:'File name',
		fileOrFolder:0,
		panelPos:{
			left:0,
			top:0
		},
	}),
	methods:{
		toggleFolders(e,path){
			this.selectedFolderPath=path;
			this.clicks++;
			const currentTarget = e.currentTarget;
			const caret = currentTarget.querySelector('.caret');
			const caretClasses = caret.className;
			let classes = currentTarget.className;
			const that = this;
			setTimeout(()=>{
				if(that.clicks === 0){
					return;
				}
				if(that.clicks === 2){
					
					const ind = classes.indexOf('open');
					if( ind > -1){
						currentTarget.className = "folders";
						caret.className = "caret";
					}else{
						currentTarget.className = classes+" open";
						caret.className = caretClasses+" open";
					} 
				}
				else{
					that.toggleCreatePanel(currentTarget);
				}
				that.clicks = 0;
			},200);

		},
		toggleCreatePanel(target){
			if(this.showCreatePanel){
				this.showCreatePanel = false;
				this.createFolderEnabled = false;
				return;
			}
			const rect = target.getBoundingClientRect();
			this.showCreatePanel = true;
			this.panelPos = {left:`${rect.x + 35}px`,top:`${rect.y + 24}px`};
		},
		setFolderCreateOption(enable){
			this.createFolderEnabled = enable;
		},
		openFile(e, filePath){
			e.stopPropagation()
			this.setFileSelected(filePath);
			const file = e.currentTarget;
			const allFiles = document.querySelectorAll('.files.open');
			allFiles.forEach(n=>{
				n.className = 'files';
			})
			file.className = 'files open';
		},
		openFileFolderNamePanel(fileOrFolder){
			//if folder option is selected
			if(fileOrFolder === 1){
				this.fileFolderNameLabel = 'Folder Name';
			}
			else{
				this.fileFolderNameLabel = 'File Name'
			}
			this.fileOrFolder = fileOrFolder;
			this.toggleFileFolderNamePanel();
		},
		createFilesOrFolder(){
			const folderStructure = this.folderStructure || {files:[],folders:[]};
			if(this.fileOrFolder === 1){
				folderStructure.folders.push({name:this.fileFolderName,files:[]})
			}
			else{
				const subFolder = this.selectedFolderPath[1];
				//creating Files on root folder
				if(!subFolder){
					folderStructure.files.push(this.fileFolderName);
				}
				//createing files inside sub folder
				else{
					folderStructure.folders[subFolder].files.push(this.fileFolderName);
				}
			}
			this.setFolderStructure(folderStructure);
			this.toggleFileFolderNamePanel();
		},
		toggleFileFolderNamePanel(){
			this.showCreatePanel = false;
			if(this.showFileFolderNamePanel){
				this.showFileFolderNamePanel = false;
				return
			}
			this.showFileFolderNamePanel = true;
		},
		setClass(path){
			if(this.selectedFilePath === path){
				return ['open']
			}
			return [];
		}

	}
}
</script>
<style scoped src="./Sidebar.css">
	
</style>