import axios from 'axios';
const host = 'http://ec2-18-219-109-248.us-east-2.compute.amazonaws.com:8081';
function getHeaders(){
	const token = window.localStorage.getItem('access-token') || '';
	const headers = {
		headers:{
			'access-token':token,
			'Content-type':'application/json'
		}
	}
	return headers;

}

function login(username,password){
	return axios.post(`${host}/login`,{
		"username":username,
		"password":password
	});
}

function saveFolderStructure(fileObj){
	return axios.post(`${host}/folderStructure`,fileObj,getHeaders());
}

function getFolderStructure(){
	return axios.get(`${host}/folderStructure`,getHeaders());
}

function getFile(path){
	const url = `${host}/getFile?file=${path}`
	return axios.get(url,getHeaders())
}

function saveFiles(filesObj){
	return axios.post(`${host}/saveFiles`,filesObj,getHeaders())
}

export {login, saveFolderStructure, getFolderStructure, getFile, saveFiles}

