<template>
	<div class="container">
		<div class="form">
			<input type="text" class="login-input" placeholder="Username" v-model="username" />
			<input type="password" class="login-input" placeholder="password" v-model="password" />
			<button class="login-btn" @click="loginHandle">Login</button>
			<span class="error-txt" v-show="hasError">Error</span>
		</div>
	</div>
</template>
<script>
import { login } from './../../services/index.js';
export default {
	props:{
		goToDashboard:Function,
		toggleLoader:Function
	},
	data:()=>({
		hasError:false,
		username:'',
		password:''
	}),
	methods:{
		loginHandle(){
			this.toggleLoader();
			login(this.username,this.password).then(resp=>{
				if(resp.data && resp.data.token){
					this.goToDashboard();
					window.localStorage.setItem('access-token',resp.data.token);
				}
			},null).finally(()=>{
				this.toggleLoader();
			})
		}
	}
}
</script>
<style scoped src="./Login.css">
</style>